<template>
    <div class="modal" :class="{ active: isActive, hidden: !isActive }">
      <div class="modal-header">
        <h2>Êé°Ëúú‰ªªÂãô‰∏≠ÂøÉ</h2>
        <button class="modal-close" @click="$emit('close-modal')">&times;</button>
      </div>
      <div class="modal-content">
        <div class="modal-tabs">
          <button 
            v-for="(tab, index) in tabs" 
            :key="index"
            :class="['tab-button', { active: activeTab === tab.id }]"
            @click="activeTab = tab.id">
            {{ tab.name }}
          </button>
        </div>
  
        <div class="tab-content">
          <!-- Êé°ËúúÂçÄÂüüÊ®ôÁ±§È†Å -->
          <div :class="['tab-pane', { active: activeTab === 'areas-tab' }]">
            <div class="area-selection">
              <h3>ÈÅ∏ÊìáÊé°ËúúÂçÄÂüü</h3>
              <div class="area-grid">
                <div 
                  v-for="area in areas" 
                  :key="area.id"
                  :class="['area-card', { 
                    locked: area.locked, 
                    selected: selectedArea === area.id 
                  }]"
                  :data-area="area.id"
                  @click="selectArea(area)">
                  <div :class="['area-image', area.id]"></div>
                  <h4>{{ area.name }}</h4>
                  <p>{{ area.description }}</p>
                  <div v-if="!area.locked" class="area-stats">
                    <span class="area-stat">Êî∂ÁõäÔºö<span class="yield">{{ area.yield }}</span></span>
                    <span class="area-stat">È¢®Èö™Ôºö<span :class="['risk', area.riskLevel]">{{ area.risk }}</span></span>
                    <span class="area-stat">ÊôÇÈñìÔºö<span class="time">{{ area.time }}</span></span>
                  </div>
                  <div v-if="area.locked" class="lock-icon">üîí</div>
                </div>
              </div>
            </div>
  
            <div class="mission-setup">
              <h3>Ê¥æÈÅ£Êé°ËúúÈöä‰ºç</h3>
              <div class="mission-form">
                <div class="form-group">
                  <label for="bees-assigned">ÊåáÊ¥æËúúËúÇÊï∏ÈáèÔºö</label>
                  <div class="slider-container">
                    <input
                      type="range"
                      id="bees-assigned"
                      :min="1"
                      :max="maxBeesAssignable"
                      v-model="beesAssigned"
                      class="slider"
                    />
                    <span>{{ beesAssigned }}</span> Èöª
                  </div>
                </div>
                <div class="form-group">
                  <label for="mission-duration">Êé°ÈõÜÊôÇÈñìÔºö</label>
                  <select id="mission-duration" v-model="missionDuration">
                    <option value="short">Áü≠Á®ã(30Áßí)</option>
                    <option value="medium">‰∏≠Á®ã(60Áßí)</option>
                    <option value="long">Èï∑Á®ã(120Áßí)</option>
                  </select>
                </div>
                <div class="form-info">
                  <div>È†êË®àÊî∂Á©´Ôºö<span>{{ expectedYield }}</span></div>
                  <div>È¢®Èö™Ë©ï‰º∞Ôºö<span>{{ missionRisk }}</span></div>
                </div>
                <button
                  @click="startMission"
                  class="game-button mission-button"
                  :disabled="!canStartMission">
                  ÈñãÂßãÊé°Ëúú‰ªªÂãô
                </button>
              </div>
            </div>
          </div>
  
          <!-- ÈÄ≤Ë°å‰∏≠‰ªªÂãôÊ®ôÁ±§È†Å -->
          <div :class="['tab-pane', { active: activeTab === 'missions-tab' }]">
            <h3>ÈÄ≤Ë°å‰∏≠ÁöÑ‰ªªÂãô</h3>
            <div class="missions-container">
              <div v-if="gameState.foraging.activeMissions.length === 0" class="empty-missions">
                ÁõÆÂâçÊ≤íÊúâÈÄ≤Ë°å‰∏≠ÁöÑÊé°Ëúú‰ªªÂãô
              </div>
              <div 
                v-for="(mission, index) in gameState.foraging.activeMissions" 
                :key="index"
                class="mission-card">
                <div class="mission-header">
                  <div>
                    <div class="mission-title">Êé°Ëúú‰ªªÂãô #{{ index + 1 }}</div>
                    <div class="mission-area">{{ getAreaName(mission.area) }}</div>
                  </div>
                  <div class="mission-timer">{{ formatTime(mission.timeRemaining) }}</div>
                </div>
                <div class="mission-details">
                  <div>ÊåáÊ¥æËúúËúÇÔºö{{ mission.bees }} Èöª</div>
                  <div>È¢®Èö™Á≠âÁ¥öÔºö{{ mission.risk }}</div>
                </div>
                <div class="mission-progress">
                  <div class="progress-bar" :style="{ width: mission.progress + '%' }"></div>
                </div>
              </div>
            </div>
          </div>
  
          <!-- Ëä±ËúúÊî∂ÈõÜÊ®ôÁ±§È†Å -->
          <div :class="['tab-pane', { active: activeTab === 'honey-tab' }]">
            <h3>Êé°ÈõÜÂà∞ÁöÑËä±Ëúú</h3>
            <div class="honey-types">
              <div v-for="honey in honeyTypes" :key="honey.id" class="honey-type">
                <div :class="['honey-image', honey.id]"></div>
                <div class="honey-info">
                  <h4>{{ honey.name }}</h4>
                  <div>Êï∏Èáè: <span>{{ getHoneyAmount(honey.id) }}</span> kg</div>
                  <div>Â∏ÇÂÉπ: <span>{{ honey.price }}</span> ÂÖÉ/kg</div>
                </div>
              </div>
            </div>
            <button @click="$emit('sell-all-honey')" class="game-button">
              Âá∫ÂîÆÊâÄÊúâËä±Ëúú
            </button>
          </div>
        </div>
      </div>
    </div>
  </template>
  
  <script>
  export default {
    name: 'ForagingModal',
    props: {
      gameState: {
        type: Object,
        required: true
      },
      isActive: {
        type: Boolean,
        default: false
      }
    },
    data() {
      return {
        activeTab: 'areas-tab',
        beesAssigned: 1,
        missionDuration: 'short',
        selectedArea: null, // Êú¨Âú∞ËøΩËπ§ÈÅ∏ÊìáÁöÑÂçÄÂüüÔºåËÄå‰∏çÊòØÁõ¥Êé•‰øÆÊîπprop
        tabs: [
          { id: 'areas-tab', name: 'Êé°ËúúÂçÄÂüü' },
          { id: 'missions-tab', name: 'ÈÄ≤Ë°å‰∏≠‰ªªÂãô' },
          { id: 'honey-tab', name: 'Ëä±ËúúÊî∂ÈõÜ' }
        ],
        areas: [
          { 
            id: 'meadow', 
            name: 'Ëä±ËçâËçâÂú∞', 
            description: 'Â∏∏Ë¶ãËä±ËúúÔºåÂÆâÂÖ®ÊÄßÈ´ò',
            yield: 'ÊôÆÈÄö',
            risk: '‰Ωé',
            riskLevel: 'low',
            time: 'Áü≠',
            locked: false
          },
          { 
            id: 'forest', 
            name: 'Ê∑±ÊûóÂ±±Ë∞∑', 
            description: 'ÈáéËä±ËúúÊ∫êÔºåÂìÅË≥™ËºÉ‰Ω≥',
            yield: 'ËâØÂ•Ω',
            risk: '‰∏≠',
            riskLevel: 'medium',
            time: '‰∏≠',
            locked: false
          },
          { 
            id: 'mountain', 
            name: 'È´òÂ±±ËçâÂéü', 
            description: 'È´òÊµ∑ÊãîËúúÊ∫êÔºåÁ®ÄÊúâËä±Á®Æ',
            yield: 'ÂÑ™Ë≥™',
            risk: 'È´ò',
            riskLevel: 'high',
            time: 'Èï∑',
            locked: false
          },
          { 
            id: 'orchard', 
            name: 'ÊûúÂúíËä±ÂçÄ', 
            description: 'ÈúÄÈÅîÂà∞ËúÇÂ∑¢3Á¥öËß£Èéñ',
            locked: true,
            minLevel: 3
          },
          { 
            id: 'rare', 
            name: 'Á®ÄÊúâËä±Âúí', 
            description: 'ÈúÄÈÅîÂà∞ËúÇÂ∑¢5Á¥öËß£Èéñ',
            locked: true,
            minLevel: 5
          }
        ],
        honeyTypes: [
          { id: 'common', name: 'ÊôÆÈÄöËä±Ëúú', price: 10 },
          { id: 'wildflower', name: 'ÈáéËä±Ëúú', price: 15 },
          { id: 'mountain', name: 'È´òÂ±±Ëä±Ëúú', price: 25 },
          { id: 'rare', name: 'Á®ÄÊúâËä±Ëúú', price: 50 }
        ]
      };
    },
    computed: {
      maxBeesAssignable() {
        // ÊúÄÂ§öÂèØÊåáÊ¥æÁöÑËúúËúÇÊï∏Èáè‰∏çËÉΩË∂ÖÈÅéÁé©ÂÆ∂ÊìÅÊúâÁöÑËúúËúÇÊï∏Èáè
        return Math.min(5, this.gameState.bees);
      },
      expectedYield() {
        if (!this.selectedArea) return 'Ë´ãÈÅ∏ÊìáÂçÄÂüü';
        
        const area = this.areas.find(a => a.id === this.selectedArea);
        let yieldValue = '';
        
        switch (area.id) {
          case 'meadow':
            yieldValue = this.beesAssigned <= 2 ? 'Â∞ëÈáèÊôÆÈÄöËä±Ëúú' : '‰∏≠ÈáèÊôÆÈÄöËä±Ëúú';
            break;
          case 'forest':
            yieldValue = this.beesAssigned <= 2 ? 'Â∞ëÈáèÈáéËä±Ëúú' : '‰∏≠ÈáèÈáéËä±Ëúú';
            break;
          case 'mountain':
            yieldValue = this.beesAssigned <= 2 ? 'Â∞ëÈáèÈ´òÂ±±Ëä±Ëúú' : '‰∏≠ÈáèÈ´òÂ±±Ëä±Ëúú';
            break;
          default:
            yieldValue = 'Â∞ëÈáèÊôÆÈÄöËä±Ëúú';
        }
        
        if (this.missionDuration === 'medium') {
          yieldValue = yieldValue.replace('Â∞ëÈáè', '‰∏≠Èáè').replace('‰∏≠Èáè', 'Â§ßÈáè');
        } else if (this.missionDuration === 'long') {
          yieldValue = yieldValue.replace('Â∞ëÈáè', 'Â§ßÈáè').replace('‰∏≠Èáè', 'Ë±êÂØå');
        }
        
        return yieldValue;
      },
      missionRisk() {
        if (!this.selectedArea) return 'Êú™Áü•';
        
        const area = this.areas.find(a => a.id === this.selectedArea);
        let risk = '';
        
        switch (area.riskLevel) {
          case 'low':
            risk = this.beesAssigned <= 2 ? 'Ê•µ‰Ωé' : '‰Ωé';
            break;
          case 'medium':
            risk = this.beesAssigned <= 2 ? '‰Ωé' : '‰∏≠';
            break;
          case 'high':
            risk = this.beesAssigned <= 2 ? '‰∏≠' : 'È´ò';
            break;
          default:
            risk = 'Êú™Áü•';
        }
        
        if (this.missionDuration === 'medium') {
          if (risk === 'Ê•µ‰Ωé') risk = '‰Ωé';
          else if (risk === '‰Ωé') risk = '‰∏≠';
          else if (risk === '‰∏≠') risk = 'È´ò';
        } else if (this.missionDuration === 'long') {
          if (risk === 'Ê•µ‰Ωé') risk = '‰∏≠';
          else if (risk === '‰Ωé') risk = 'È´ò';
          else if (risk === '‰∏≠') risk = 'Ê•µÈ´ò';
        }
        
        return risk;
      },
      canStartMission() {
        return (
          this.selectedArea &&
          this.beesAssigned > 0 &&
          this.beesAssigned <= this.gameState.bees
        );
      }
    },
    mounted() {
      // Êõ¥Êñ∞ÂçÄÂüüËß£ÈéñÁãÄÊÖã
      this.checkAndUnlockAreas();
      
      // ÂàùÂßãÂåñÊú¨Âú∞ÂçÄÂüüÈÅ∏ÊìáÁãÄÊÖãÔºåËàá gameState ‰øùÊåÅÂêåÊ≠•
      this.selectedArea = this.gameState.foraging.selectedArea;
    },
    watch: {
      // Áï∂ gameState.foraging.selectedArea ËÆäÂåñÊôÇÂêåÊ≠•Âà∞Êú¨Âú∞ÁãÄÊÖã
      'gameState.foraging.selectedArea'(newVal) {
        this.selectedArea = newVal;
      }
    },
    methods: {
      selectArea(area) {
        if (area.locked) return;
        
        // Êõ¥Êñ∞Êú¨Âú∞ÈÅ∏ÊìáÁãÄÊÖãÔºåËÄå‰∏çÊòØÁõ¥Êé•‰øÆÊîπ prop
        this.selectedArea = area.id;
        
        // ÈÄöÁü•Áà∂ÁµÑ‰ª∂ÂçÄÂüüÈÅ∏ÊìáÂ∑≤Êõ¥Êîπ
        this.$emit('update-selected-area', area.id);
      },
      
      startMission() {
        if (!this.canStartMission) return;
        
        const missionData = {
          area: this.selectedArea,
          bees: parseInt(this.beesAssigned),
          duration: this.missionDuration,
          risk: this.missionRisk
        };
        
        this.$emit('start-mission', missionData);
      },
      
      checkAndUnlockAreas() {
        // Ê†πÊìöËúÇÂ∑¢Á≠âÁ¥öËß£ÈéñÂçÄÂüüÔºå‰ΩÜÂè™Êõ¥Êñ∞Êú¨Âú∞Êï∏Êìö
        this.areas.forEach(area => {
          if (area.minLevel && this.gameState.hiveLevel >= area.minLevel) {
            area.locked = false;
          }
        });
      },
      
      getAreaName(areaId) {
        const area = this.areas.find(a => a.id === areaId);
        return area ? area.name : 'Êú™Áü•ÂçÄÂüü';
      },
      
      formatTime(seconds) {
        if (!seconds && seconds !== 0) return '--:--';
        
        const mins = Math.floor(seconds / 60);
        const secs = Math.floor(seconds % 60);
        return `${mins.toString().padStart(2, '0')}:${secs.toString().padStart(2, '0')}`;
      },
      
      getHoneyAmount(honeyType) {
        // ÂÆâÂÖ®Âú∞Áç≤ÂèñËúÇËúúÊï∏ÈáèÔºåÈÅøÂÖçÊú™ÂÆöÁæ©ÈåØË™§
        if (this.gameState && 
            this.gameState.foraging && 
            this.gameState.foraging.honeyCollected && 
            this.gameState.foraging.honeyCollected[honeyType] !== undefined) {
          return this.gameState.foraging.honeyCollected[honeyType].toFixed(1);
        }
        return '0.0';
      }
    }
  };
  </script>
  
  <style scoped>
  /* ÈÄôË£°ÂèØ‰ª•ÊîæÁΩÆÁµÑ‰ª∂ÁâπÂÆöÊ®£ÂºèÔºå‰ΩÜÂ§ßÈÉ®ÂàÜÊ®£ÂºèÂ∑≤Âú®ÂÖ®Â±ÄCSS‰∏≠ÂÆöÁæ© */
  .modal-tabs {
    display: flex;
    background-color: #f5f0e5;
    border-bottom: 1px solid #e0d8c8;
  }
  
  .tab-button {
    padding: 12px 20px;
    background: none;
    border: none;
    border-bottom: 3px solid transparent;
    font-size: 1rem;
    color: #8c6d3c;
