    transition: all 0.2s;
  }
  
  .tab-button:hover {
    background-color: #f8f5ee;
    color: #5d4215;
  }
  
  .tab-button.active {
    border-bottom-color: #ffc226;
    color: #5d4215;
    font-weight: bold;
  }
  
  .tab-content {
    padding: 20px;
  }
  
  .tab-pane {
    display: none;
  }
  
  .tab-pane.active {
    display: block;
  }
  
  .honey-types {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(220px, 1fr));
    gap: 15px;
    margin: 15px 0;
  }
  
  .area-card.selected {
    background-color: #fff0c8;
    box-shadow: 0 0 0 2px #ffb300, 0 5px 15px rgba(0, 0, 0, 0.1);
  }
  </style>