          honeyPerBeePerMinute: 0.1, // ÊØèÈöªËúúËúÇÊØèÂàÜÈêòÁî¢ËúúÈáè

          // ÈÅäÊà≤ÂÖßÊôÇÈñìË®àÊï∏Âô®
