            selectedArea: null,
            beesAssigned: 1,
            honeyCollected: {
              common: 0,
              wildflower: 0,
              mountain: 0,
              rare: 0,
            },
          },
        },

        // Â§©Ê∞£È°ûÂûãÂèäÂÖ∂ÂΩ±Èüø
        weatherTypes: {
          sunny: {
            icon: "‚òÄÔ∏è",
            name: "Êô¥Â§©",
            effect: 1.0,
            description: "ÈôΩÂÖâÂÖÖË∂≥ÔºåËúúËúÇÂá∫Âã§ÁéáÈ´ò",
          },
          rainy: {
            icon: "üåßÔ∏è",
            name: "ÈÄ£Á∫åÂ§ßÈõ®",
            effect: 0.3,
            description: "Èõ®Â§©ÔºåËúúËúÇÈõ£‰ª•È£õË°åÊé°Ëúú",
          },
          drought: {
            icon: "üèúÔ∏è",
            name: "‰πæÊó±",
            effect: 0.5,
            description: "ËúúÊ∫êÊ§çÁâ©ÊûØËêéÔºåËä±ËúúÊ∏õÂ∞ë",
          },
          cold: {
            icon: "‚ùÑÔ∏è",
            name: "ÂØíÊµÅ‰æÜË•≤",
            effect: 0.2,
            description: "ËúúËúÇÊ¥ªÂãïÊ∏õÁ∑©ÔºåÈúÄË¶ÅÈ°çÂ§ñ‰æõÊöñ",
          },
        },

        // ËúÇËúúÂ∏ÇÂ†¥ÂÉπÊ†ºÊ≥¢Âãï
        honeyMarketPrice: 10, // ÂàùÂßãÂÉπÊ†ºÔºö10ÂÖÉ/kg

        // È§äËúÇÁü•Ë≠òÂ∫´
        beeFacts: [
          "‰∏ÄÈöªÂ∑•ËúÇ‰∏ÄÁîü‰∏≠Âè™ËÉΩÁîüÁî¢Á¥Ñ1Ëå∂ÂåôÁöÑËúÇËúú„ÄÇ",
          "ËúÇÂêé‰∏ÄÂ§©ÂèØ‰ª•Áî¢‰∏ãÂ§öÈÅî2000È°ÜËõãÔºåÊòØËá™Â∑±È´îÈáçÂÖ©ÂÄçÁöÑËõãÈáè„ÄÇ",
          "Â∑•ËúÇÁöÑÂ£ΩÂëΩÂÉÖÁ¥ÑÁÇ∫6ÈÄ±Ôºå‰ΩÜÂú®ÂÜ¨Â≠£ÂèØ‰ª•Â≠òÊ¥ªÊï∏Êúà„ÄÇ",
          "ËúúËúÇÈúÄË¶ÅÊé°ÈõÜÁ¥Ñ2ÁôæËê¨ÊúµËä±ÔºåÊâçËÉΩË£ΩÈÄ†Âá∫1ÂÖ¨Êñ§ÁöÑËúÇËúú„ÄÇ",
          "ËúúËúÇÊòØÂîØ‰∏ÄÊúÉÁî¢Áîü‰∫∫È°ûÂèØ‰ª•È£üÁî®ÁöÑÊòÜËü≤„ÄÇ",
          "ËúúËúÇÁöÑÈ£õË°åÈÄüÂ∫¶ÂèØÈÅîÊØèÂ∞èÊôÇ24ÂÖ¨Èáå„ÄÇ",
          "ËúÇÂ∑¢ÂÖßÊ∫´Â∫¶ÊÅÜÂÆöÂú®35¬∞CÂ∑¶Âè≥ÔºåÂÜ¨Â≠£ËúúËúÇÊúÉËÅöÈõÜÊàêÂúòÁ∂≠ÊåÅÊ∫´Â∫¶„ÄÇ",
          "‰∏ÄÂÄãÂÅ•Â∫∑ÁöÑËúÇÁæ§ÂèØÊúâ50,000Âà∞60,000ÈöªËúúËúÇ„ÄÇ",
          "ËúúËúÇÈÄèÈÅé„ÄåÊêñÊì∫Ëàû„ÄçÂëäË®¥ÂÖ∂‰ªñËúúËúÇÈ£üÁâ©‰æÜÊ∫êÁöÑÊñπÂêëËàáË∑ùÈõ¢„ÄÇ",
          "ÂÖ®ÁêÉÁ¥Ñ75%ÁöÑËæ≤‰ΩúÁâ©Âú®ÊüêÁ®ÆÁ®ãÂ∫¶‰∏ä‰æùË≥¥ËúúËúÇÊéàÁ≤â„ÄÇ",
          "Áèæ‰ª£È§äËúÇ‰∫∫Èù¢Ëá®ÁöÑÊúÄÂ§ßÊåëÊà∞‰πã‰∏ÄÊòØ„ÄåËúÇÁæ§Â¥©ÊΩ∞Â§±Ë™øÁóá„ÄçÔºåÈÄ†ÊàêÂ§ßÈáèËúúËúÇÁ•ûÁßòÊ∂àÂ§±„ÄÇ",
          "ËúÇËúúÊ∞∏ÈÅ†‰∏çÊúÉËÆäË≥™ÔºåËÄÉÂè§Â≠∏ÂÆ∂Âú®Âè§ÂüÉÂèäÂè§Â¢ì‰∏≠ÁôºÁèæÁöÑËúÇËúúÔºåÊï∏ÂçÉÂπ¥Âæå‰ªçÂèØÈ£üÁî®„ÄÇ",
        ],
        currentBeeFact: "",

        // Ê®°ÊÖãÊ°ÜÁãÄÊÖã
        isModalActive: false,
        isForagingModalActive: false,

        // ÂãûÂäõ‰ªªÂãô
        laborTask: {
          isActive: false,
          name: "",
          clicksNeeded: 0,
          clicksDone: 0,
          onComplete: null,
        },

        // ÊåëÊà∞È°ØÁ§∫ÁãÄÊÖã
        challengeHidden: false,

        // ÁóÖËü≤ÂÆ≥È°ûÂûãÂíåÁí∞Â¢ÉÂΩ±ÈüøÈ°ûÂûãÂ∞áÂú®mounted‰∏≠ÂÆöÁæ©
        pestTypes: [],
        environmentalEvents: [],

        // ÈÅäÊà≤Âæ™Áí∞Ë®àÊôÇÂô®
        gameLoopInterval: null,
        saveGameInterval: null,
      };
    },
    computed: {
      healthColorStyle() {
        if (this.gameState.hiveHealth < 30) {
          return { color: "#e53935" }; // Á¥ÖËâ≤Ë≠¶Âëä
        } else if (this.gameState.hiveHealth < 60) {
          return { color: "#ff9800" }; // Ê©ôËâ≤ÊèêÁ§∫
        } else {
          return { color: "#4caf50" }; // Á∂†Ëâ≤Ê≠£Â∏∏
        }
      },
    },
    mounted() {
      this.initGame();
    },
    beforeUnmount() {
      // Ê∏ÖÈô§ÊâÄÊúâË®àÊôÇÂô®
      clearInterval(this.gameLoopInterval);
      clearInterval(this.saveGameInterval);
    },
    methods: {
      initGame() {
        // ÂàùÂßãÂåñÁóÖËü≤ÂÆ≥È°ûÂûã
        this.initPestTypes();
        // ÂàùÂßãÂåñÁí∞Â¢ÉÂΩ±ÈüøÈ°ûÂûã
        this.initEnvironmentalEvents();

        // ËºâÂÖ•Â∑≤‰øùÂ≠òÁöÑÈÅäÊà≤Êï∏Êìö
        this.loadGame();

        // Ë®≠ÁΩÆÈÅäÊà≤Âæ™Áí∞
        this.gameLoopInterval = setInterval(this.gameLoop, 1000);

        // Ë®≠ÁΩÆËá™Âãï‰øùÂ≠ò
        this.saveGameInterval = setInterval(this.saveGame, 30000);

        // ÂàùÂßãÂåñÈö®Ê©ü‰∫ã‰ª∂
        setInterval(this.maybeStartChallenge, 120000);

        // ÂàùÂßãÂåñÈ§äËúÇÁü•Ë≠ò
        this.currentBeeFact =
          this.beeFacts[Math.floor(Math.random() * this.beeFacts.length)];
        setInterval(this.updateBeeFact, 60000);

        // Ê∏≤ÊüìËúúËúÇ
        this.$nextTick(() => {
          this.renderBees();
        });

        // È°ØÁ§∫Ê≠°ËøéË®äÊÅØ
        this.showNotification(
          "Ê≠°Ëøé‰æÜÂà∞È§äËúÇÈÅî‰∫∫ÔºÅÈñãÂßãÁ∂ìÁáü‰Ω†ÁöÑÁ¨¨‰∏ÄÂÄãËúÇÂ†¥ÂêßÔºÅ",
          "info"
        );

        // ÁÇ∫‰∫ÜÁ§∫ÁØÑÔºåÁ´ãÂç≥Ëß∏Áôº‰∏ÄÂÄãÁ∞°ÂñÆÁöÑÂ§©Ê∞£ËÆäÂåñ
        setTimeout(() => {
          this.changeWeather("rainy");
        }, 10000);
      },

      // ÈÅäÊà≤Âæ™Áí∞
      gameLoop() {
        // Ë®àÁÆóÁ∂ìÈÅéÁöÑÈÅäÊà≤ÊôÇÈñì
        this.gameState.gameTime++;

        // ÁîüÁî¢ËúÇËúú (ÊØèÁßíÂü∑Ë°å‰∏ÄÊ¨°ÔºåÊâÄ‰ª•Èô§‰ª•60‰æÜÂæóÂà∞ÊØèÂàÜÈêòÁöÑËúÇËúúÈáè)
        let honeyProduction =
          (this.gameState.bees *
            this.gameState.honeyPerBeePerMinute *
            this.gameState.weather.effectMultiplier) /
          60;
        this.gameState.honey += honeyProduction;
        this.gameState.totalHoneyCollected += honeyProduction;

        // Â§©Ê∞£Á≥ªÁµ±Êõ¥Êñ∞
        if (this.gameState.weather.daysRemaining > 0) {
          this.gameState.weather.daysRemaining--;
          if (this.gameState.weather.daysRemaining === 0) {
            this.changeWeather("sunny"); // ÈªòË™çÊÅ¢Âæ©Êô¥Â§©
          }
        }

        // Áï∂ËúÇÁæ§ÂÅ•Â∫∑Â∫¶‰ΩéÊôÇÔºåÂèØËÉΩÊúÉÊêçÂ§±ËúúËúÇ
        if (
          this.gameState.hiveHealth < 30 &&
          this.gameState.bees > 0 &&
          Math.random() < 0.05
        ) {
          this.gameState.bees--;
          this.showNotification("ËúÇÁæ§ÂÅ•Â∫∑ÁãÄÊ≥Å‰∏ç‰Ω≥ÔºåÂ§±Âéª‰∫Ü1ÈöªËúúËúÇÔºÅ", "warning");
          this.renderBees();
        }
      },

      // Êé°ÈõÜËúÇËúú
      collectHoney() {
        if (this.gameState.honey <= 0) return;

        // Êé°ÈõÜËúÇËúúÈúÄË¶Å‰ªòÂá∫ÂãûÂäõ (ÈªûÊìäÊ¨°Êï∏)
        this.startLaborTask("Êé°ÈõÜËúÇËúú", 5, () => {
          // Ë®àÁÆóËúÇËúúÂÉπÂÄº
          const honeyValue = Math.round(
            this.gameState.honey * this.honeyMarketPrice
          );
          this.gameState.money += honeyValue;

          this.showNotification(
            `ÊàêÂäüÊî∂Á©´‰∫Ü ${this.gameState.honey.toFixed(
              1
            )}kg ËúÇËúúÔºåË≥£Âá∫Áç≤Âæó ${honeyValue} ÂÖÉÔºÅ`,
            "success"
          );

          // ÊØèÊ¨°Êî∂Á©´ÊúÉÊõ¥Êñ∞Â∏ÇÂ†¥ÂÉπÊ†º
          this.updateHoneyMarketPrice();

          this.gameState.honey = 0;
        });
      },

      // Ë≥ºË≤∑ËúúËúÇ
      buyBee() {
        if (
          this.gameState.money < this.gameState.costs.bee ||
          this.gameState.bees >= this.gameState.hiveCapacity
        )
          return;

        this.gameState.money -= this.gameState.costs.bee;
        this.gameState.bees += 1;

        this.showNotification("Ë≥ºË≤∑‰∫Ü1ÈöªÊñ∞ËúúËúÇÔºÅ", "success");
        this.renderBees();
      },

      // ÂçáÁ¥öËúÇÂ∑¢
      upgradeHive() {
        if (this.gameState.money < this.gameState.costs.hiveUpgrade) return;

        this.gameState.money -= this.gameState.costs.hiveUpgrade;
        this.gameState.hiveLevel += 1;
        this.gameState.hiveCapacity += 5;
        this.gameState.honeyPerBeePerMinute += 0.05;

        // ÂçáÁ¥öÂæåÂ¢ûÂä†ÊàêÊú¨
        this.gameState.costs.hiveUpgrade = Math.floor(
          this.gameState.costs.hiveUpgrade * 1.5
        );

        this.showNotification(
          `ËúÇÂ∑¢ÂçáÁ¥öÂà∞Á≠âÁ¥ö ${this.gameState.hiveLevel}ÔºÅÂÆπÈáèÂíåÊïàÁéáÊèêÂçá‰∫Ü`,
          "success"
        );
      },

      // Ê™¢Êü•ËúÇÂ∑¢
      checkHive() {
        // Ê™¢Êü•ËúÇÂ∑¢ÈúÄË¶Å‰ªòÂá∫ÂãûÂäõ (Êõ¥Èï∑ÁöÑÈªûÊìäÊôÇÈñì)
        this.startLaborTask("Ê™¢Êü•ËúÇÂ∑¢", 8, () => {
          // Ê™¢Êü•ÂèØ‰ª•ÁôºÁèæÂïèÈ°å‰∏¶Áï•ÂæÆÊèêÂçáËúÇÁæ§ÂÅ•Â∫∑
          this.gameState.hiveHealth = Math.min(
            100,
            this.gameState.hiveHealth + 5
          );

          // Èö®Ê©üÁôºÁèæÂïèÈ°å
          if (Math.random() < 0.3 && !this.gameState.currentChallenge) {
            let problemFound = false;

            if (Math.random() < 0.5) {
              // ÁôºÁèæÁóÖËü≤ÂÆ≥ÂïèÈ°å
              let pest =
                this.pestTypes[
                  Math.floor(Math.random() * this.pestTypes.length)
                ];
              this.startChallenge("pest", pest);
              problemFound = true;
            }

            if (!problemFound) {
              this.showNotification("Ê™¢Êü•ÂÆåÊàêÔºåËúÇÂ∑¢ÁãÄÊ≥ÅËâØÂ•ΩÔºÅ", "success");
            }
          } else {
            this.showNotification("Ê™¢Êü•ÂÆåÊàêÔºåËúÇÂ∑¢ÁãÄÊ≥ÅËâØÂ•ΩÔºÅ", "success");
          }
        });
      },

      // ÈñãÂßãÊé°Ëúú‰ªªÂãô
      startForagingMission(missionData) {
        // ÈÄôÂÄãÊñπÊ≥ïÂ∞áÁî±ForagingModalÁµÑ‰ª∂Ë™øÁî®
        console.log("Starting foraging mission:", missionData);
        // ÂØ¶Áèæ‰ªªÂãôÈÇèËºØ...
      },

      // Âá∫ÂîÆÊâÄÊúâËä±Ëúú
      sellAllHoney() {
        // ÈÄôÂÄãÊñπÊ≥ïÂ∞áÁî±ForagingModalÁµÑ‰ª∂Ë™øÁî®
        console.log("Selling all honey");
        // ÂØ¶ÁèæÂá∫ÂîÆÈÇèËºØ...
      },

      updateSelectedArea(areaId) {
        this.gameState.foraging.selectedArea = areaId;
      },

      // ÈñãÂïüÊé°ËúúÊ®°ÊÖãÊ°Ü
      openForagingModal() {
        this.isModalActive = true;
        this.isForagingModalActive = true;
      },

      // ÈóúÈñâÊé°ËúúÊ®°ÊÖãÊ°Ü
      closeForagingModal() {
        this.isForagingModalActive = false;
        this.isModalActive = false;
      },

      // ÈóúÈñâÊâÄÊúâÊ®°ÊÖãÊ°Ü
      closeAllModals(event) {
        // Âè™ÊúâÁï∂ÈªûÊìäÁöÑÊòØË¶ÜËìãÂ±§Êú¨Ë∫´ÊôÇÊâçÈóúÈñâ
        if (event.target.id === "modal-overlay") {
          this.isForagingModalActive = false;
          this.isModalActive = false;
        }
      },

      // ÈñãÂßãÂãûÂäõ‰ªªÂãô
      startLaborTask(taskName, clicksNeeded, onComplete) {
        this.laborTask = {
          isActive: true,
          name: taskName,
          clicksNeeded: clicksNeeded,
          clicksDone: 0,
          onComplete: onComplete,
        };
      },

      // ÂÆåÊàêÂãûÂäõ‰ªªÂãô
      completeLaborTask(clicksDone) {
        // Êõ¥Êñ∞‰ªªÂãôÁãÄÊÖã
        this.laborTask.clicksDone += clicksDone;
        this.laborTask.isActive = false;

        // Âü∑Ë°å‰ªªÂãôÂÆåÊàêÂæåÁöÑÈÇèËºØ
        if (this.laborTask.onComplete) {
          this.laborTask.onComplete();
        }
      },

      // È°ØÁ§∫ÈÄöÁü•
      showNotification(message, type = "info") {
        const notificationArea = document.querySelector(".notification-area");
        const notification = document.createElement("div");
        notification.className = `notification ${type}`;
        notification.textContent = message;

        notificationArea.appendChild(notification);

        // 3ÁßíÂæåËá™ÂãïÊ∂àÂ§±
        setTimeout(() => {
          notification.style.animation = "slideOut 0.3s forwards";
          setTimeout(() => {
            notification.remove();
          }, 300);
        }, 3000);
      },

      // ËÆäÊõ¥Â§©Ê∞£
      changeWeather(weatherType) {
        this.gameState.weather.current = weatherType;
        this.gameState.weather.effectMultiplier =
          this.weatherTypes[weatherType].effect;

        // Â§©Ê∞£ÊåÅÁ∫å3-7Â§©
        this.gameState.weather.daysRemaining =
          Math.floor(Math.random() * 5) + 3;

        // Â¶ÇÊûúÂ§©Ê∞£‰∏çÊòØÊô¥Â§©ÔºåÂâáÂèØËÉΩËß∏ÁôºÂ§©Ê∞£ÊåëÊà∞
        if (weatherType !== "sunny") {
          this.startWeatherChallenge(weatherType);
        }

        this.showNotification(
          `Â§©Ê∞£ËÆäÂåñÔºö${this.weatherTypes[weatherType].name}ÔºÅ${this.weatherTypes[weatherType].description}`,
          "info"
        );
      },

      // ÂïüÂãïÂ§©Ê∞£ÊåëÊà∞
      startWeatherChallenge(weatherType) {
        if (this.gameState.currentChallenge) return; // Â¶ÇÊûúÂ∑≤ÊúâÊåëÊà∞Ôºå‰∏çÂÜçËß∏ÁôºÊñ∞ÊåëÊà∞

        let challenge = {
          id: weatherType,
          title: this.weatherTypes[weatherType].name,
          description: `${
            this.weatherTypes[weatherType].description
          }ÔºåËúÇËúúÁî¢ÈáèÈôç‰ΩéÂà∞${this.weatherTypes[weatherType].effect * 100}%ÔºÅ`,
          solutions: [],
        };

        switch (weatherType) {
          case "rainy":
            challenge.solutions = [
              {
                name: "Ê∫ñÂÇôÁ≥ñÊ∞¥È§µÈ£ü (¬•50)",
                cost: 50,
                effectText: "ËúúËúÇÁç≤ÂæóÈ°çÂ§ñÁ≥ñÊ∞¥Ôºå‰øùÊåÅ‰∫ÜÊ¥ªÂäõ",
                effect: () => {
                  this.gameState.money -= 50;
                  this.gameState.weather.effectMultiplier += 0.2; // Áï•ÂæÆÊèêÂçáÊïàÁéá
                  return true;
                },
              },
              {
                name: "Á≠âÂæÖÂ§©Ê∞£Â•ΩËΩâ",
                cost: 0,
                effectText: "Âè™ËÉΩÁ≠âÂæÖÈõ®ÂÅú...",
                effect: () => {
                  return true;
                },
              },
            ];
            break;

          case "drought":
            challenge.solutions = [
              {
                name: "ÈÅ∑ÁßªËúÇÁÆ± (¬•100)",
                cost: 100,
                effectText: "ËúÇÁÆ±ÈÅ∑ÁßªÂà∞ÊúâÊõ¥Â§öËúúÊ∫êÁöÑÂú∞Êñπ",
                effect: () => {
                  this.gameState.money -= 100;
                  this.gameState.weather.effectMultiplier += 0.3;
                  return true;
                },
              },
              {
                name: "Ëá™Ë°åÁ®ÆÊ§çËúúÊ∫êÊ§çÁâ© (¬•70)",
                cost: 70,
                effectText: "Á®ÆÊ§çÁöÑËä±ÊúµÊèê‰æõ‰∫ÜÈÉ®ÂàÜËúúÊ∫ê",
                effect: () => {
                  this.gameState.money -= 70;
                  this.gameState.weather.effectMultiplier += 0.2;
                  return true;
                },
              },
            ];
            break;

          case "cold":
            challenge.solutions = [
              {
                name: "Âä†Âõ∫ËúÇÁÆ±‰øùÊ∫´ (¬•80)",
                cost: 80,
                effectText: "ËúÇÁÆ±‰øùÊ∫´ÊïàÊûúÊèêÂçáÔºåËúúËúÇÊ¥ªÂãïÂ¢ûÂä†",
                effect: () => {
                  this.gameState.money -= 80;
                  this.gameState.weather.effectMultiplier += 0.3;
                  return true;
                },
              },
              {
                name: "Ë≥ºË≤∑Á≥ñÊºøÈ§µÈ£ü (¬•60)",
                cost: 60,
                effectText: "Á≥ñÊºøÊèê‰æõËÉΩÈáèÔºåÂπ´Âä©ËúúËúÇÂ∫¶ÈÅéÂØíÂÜ∑",
                effect: () => {
                  this.gameState.money -= 60;
                  this.gameState.weather.effectMultiplier += 0.2;
                  return true;
                },
              },
            ];
            break;
        }

        this.startChallenge("weather", challenge);
      },

      // Èö®Ê©üÂèØËÉΩËß∏ÁôºÊåëÊà∞
      maybeStartChallenge() {
        if (this.gameState.currentChallenge || Math.random() > 0.3) return; // 30%Ê©üÁéáËß∏Áôº

        let challengeTypes = ["pest", "environmental"];
        let type =
          challengeTypes[Math.floor(Math.random() * challengeTypes.length)];

        if (type === "pest") {
          let pest =
            this.pestTypes[Math.floor(Math.random() * this.pestTypes.length)];
          this.startChallenge("pest", pest);
        } else if (type === "environmental") {
          let envEvent =
            this.environmentalEvents[
              Math.floor(Math.random() * this.environmentalEvents.length)
            ];
          this.startChallenge("environment", envEvent);
        }
      },

      // ÂïüÂãïÊåëÊà∞
      startChallenge(type, challenge) {
        this.gameState.currentChallenge = {
          type: type,
          data: challenge,
          startTime: Date.now(),
        };

        this.challengeHidden = false;

        // È°ØÁ§∫ÈÄöÁü•
        this.showNotification(`Ë≠¶ÂëäÔºö${challenge.title}ÔºÅ`, "warning");

        // Ë®≠ÂÆöË®àÊôÇÂô®ÔºåÂ¶ÇÊûúÊ≤íËß£Ê±∫ÂâáËß∏ÁôºÂæåÊûú
        setTimeout(() => {
          if (
            this.gameState.currentChallenge &&
            this.gameState.currentChallenge.type === type &&
            this.gameState.currentChallenge.data.id === challenge.id
          ) {
            // Âü∑Ë°åÂæåÊûú
            if (challenge.consequence) {
              challenge.consequence();
            }

            // ÁßªÈô§ÊåëÊà∞
            this.challengeHidden = true;
            this.gameState.currentChallenge = null;
          }
        }, 120000); // 2ÂàÜÈêòÂæåËß∏ÁôºÂæåÊûú
      },

      // ÊáâÁî®Ëß£Ê±∫ÊñπÊ°à
      applySolution(solution) {
        // Ê™¢Êü•ÊòØÂê¶ÊúâË∂≥Â§†ÈáëÈå¢
        if (solution.cost > this.gameState.money) {
          this.showNotification(
            `ÈáëÈå¢‰∏çË∂≥ÔºÅÈúÄË¶Å ${solution.cost} ÂÖÉ`,
            "warning"
          );
          return;
        }

        // Âü∑Ë°åËß£Ê±∫ÊñπÊ°à
        let result = solution.effect();
        if (result) {
          // Ëß£Ê±∫ÊñπÊ°àÁîüÊïà
          this.showNotification(solution.effectText, "success");
          this.gameState.challengesSolved++;

          // ÁßªÈô§ÊåëÊà∞
          this.challengeHidden = true;
          this.gameState.currentChallenge = null;
        }
      },

      // Êõ¥Êñ∞ËúÇËúúÂ∏ÇÂ†¥ÂÉπÊ†º
      updateHoneyMarketPrice() {
        // Èö®Ê©üÊ≥¢ÂãïÔºå‰ΩÜ‰øùÊåÅÂú®7-13ÂÖÉ/kg‰πãÈñì
        let change = (Math.random() - 0.5) * 3;
        this.honeyMarketPrice = Math.min(
          13,
          Math.max(7, this.honeyMarketPrice + change)
        );
        this.showNotification(
          `ËúÇËúúÂ∏ÇÂ†¥ÂÉπÊ†ºÊõ¥Êñ∞Ôºö${this.honeyMarketPrice.toFixed(1)}ÂÖÉ/kg`,
          "info"
        );
      },

      // Ê∏≤ÊüìËúúËúÇÂãïÁï´
      renderBees() {
        const hiveContainer = this.$refs.hiveContainer;
        if (!hiveContainer) return;

        // Ê∏ÖÁ©∫ÁèæÊúâËúúËúÇ
        while (hiveContainer.firstChild) {
          hiveContainer.removeChild(hiveContainer.firstChild);
        }

        // Ê∑ªÂä†ËúúËúÇ
        for (let i = 0; i < this.gameState.bees; i++) {
          const bee = document.createElement("div");
          bee.className = "bee";

          // Èö®Ê©ü‰ΩçÁΩÆ
          let left = Math.random() * 80 + 10; // 10% to 90%
          let top = Math.random() * 80 + 10; // 10% to 90%

          bee.style.left = `${left}%`;
          bee.style.top = `${top}%`;

          // Èö®Ê©üÈ£õË°åÂãïÁï´Âª∂ÈÅ≤ÔºåËÆìËúúËúÇ‰∏çÂêåÊ≠•ÁßªÂãï
          bee.style.animationDelay = `${Math.random() * 5}s`;

          hiveContainer.appendChild(bee);
        }
      },

      // Êõ¥Êñ∞È§äËúÇÁü•Ë≠ò
      updateBeeFact() {
        this.currentBeeFact =
          this.beeFacts[Math.floor(Math.random() * this.beeFacts.length)];
      },

      // ‰øùÂ≠òÈÅäÊà≤
      saveGame() {
        localStorage.setItem("beekeeperGame", JSON.stringify(this.gameState));
        this.gameState.lastSaveTime = Date.now();
      },

      // Âä†ËºâÈÅäÊà≤
      loadGame() {
        const savedGame = localStorage.getItem("beekeeperGame");
        if (savedGame) {
          try {
            const parsed = JSON.parse(savedGame);
            // Âè™ËÆÄÂèñÈúÄË¶ÅÁöÑÊï∏ÊìöÔºåÈÅøÂÖçÁâàÊú¨ÂïèÈ°å
            this.gameState.bees = parsed.bees || 0;
            this.gameState.honey = parsed.honey || 0;
            this.gameState.money = parsed.money || 100;
            this.gameState.hiveLevel = parsed.hiveLevel || 1;
            this.gameState.hiveHealth = parsed.hiveHealth || 100;
            this.gameState.hiveCapacity = parsed.hiveCapacity || 10;
            this.gameState.honeyPerBeePerMinute =
              parsed.honeyPerBeePerMinute || 0.1;
            this.gameState.totalHoneyCollected =
              parsed.totalHoneyCollected || 0;
            this.gameState.challengesSolved = parsed.challengesSolved || 0;
            this.gameState.costs = parsed.costs || {
              bee: 50,
              hiveUpgrade: 200,
            };

            // Ê™¢Ê∏¨Èõ¢Á∑öÊôÇÈñì‰∏¶Ë®àÁÆóË≥áÊ∫ê
            const currentTime = Date.now();
            const offlineTimeMinutes =
              (currentTime - (parsed.lastSaveTime || currentTime)) /
              (1000 * 60);

            if (offlineTimeMinutes > 5) {
              // Â¶ÇÊûúÈõ¢Á∑öË∂ÖÈÅé5ÂàÜÈêò
              // Ë®àÁÆóÈõ¢Á∑öÊôÇÁöÑËúÇËúúÁîüÁî¢ÔºàËÄÉÊÖÆÊïàÁéáÈôç‰ΩéÔºâ
              const offlineHoney =
                this.gameState.bees *
                this.gameState.honeyPerBeePerMinute *
                offlineTimeMinutes *
                0.5; // ÊïàÁéáÈôçÁÇ∫50%
              this.gameState.honey += offlineHoney;

              // È°ØÁ§∫Èõ¢Á∑öÊî∂Áõä
              setTimeout(() => {
                this.showNotification(
                  `‰Ω†Èõ¢Èñã‰∫Ü${Math.floor(
                    offlineTimeMinutes
                  )}ÂàÜÈêòÔºåËúúËúÇÂú®Ê≠§ÊúüÈñìÁî¢Âá∫‰∫Ü${offlineHoney.toFixed(1)}kgËúÇËúúÔºÅ`,
                  "success"
                );
              }, 1000);
            }
          } catch (e) {
            console.error("Error loading saved game", e);
            // Â¶ÇÊûúËÆÄÂèñÂá∫ÈåØÔºå‰ΩøÁî®ÈªòË™çÂÄº
          }
        }
      },

      // ÂàùÂßãÂåñÁóÖËü≤ÂÆ≥È°ûÂûã
      initPestTypes() {
        this.pestTypes = [
          {
            id: "mite",
            title: "ËúÇËû®ÁàÜÁôº",
            description: "ËúÇËû®Ê≠£Âú®‰æµÊìæ‰Ω†ÁöÑËúÇÁæ§ÔºåÂèØËÉΩÂ∞éËá¥ËúúËúÇÁîüÁóÖÊàñÊ≠ª‰∫°ÔºÅ",
            severity: "high",
            solutions: [
              {
                name: "Ë≥ºË≤∑Ëó•ÂäëÊ≤ªÁôÇ",
                cost: 150,
                effectText: "ËúÇÁæ§Â∑≤Ë¢´Ê≤ªÁôÇÔºåÂÅ•Â∫∑ÊÅ¢Âæ©‰∏≠",
                effect: () => {
                  this.gameState.money -= 150;
                  this.gameState.hiveHealth = Math.min(
                    100,
                    this.gameState.hiveHealth + 30
                  );
                  return true;
                },
              },
              {
                name: "Ëá™ÁÑ∂ÁôÇÊ≥ï (È¢®Èö™Êõ¥È´ò)",
                cost: 50,
                effectText: "Ëá™ÁÑ∂ÁôÇÊ≥ïÊïàÊûúÊúâÈôêÔºåËúÇÁæ§‰ªçÊúâÈ¢®Èö™",
                effect: () => {
                  this.gameState.money -= 50;
                  let success = Math.random() > 0.4; // 60%ÊàêÂäüÁéá
                  if (success) {
                    this.gameState.hiveHealth = Math.min(
                      100,
                      this.gameState.hiveHealth + 15
                    );
                    return true;
                  } else {
                    this.gameState.hiveHealth -= 10;
                    this.showNotification(
                      "Ëá™ÁÑ∂ÁôÇÊ≥ïÂ§±ÊïóÔºåËúÇÁæ§ÂÅ•Â∫∑Â∫¶‰∏ãÈôçÔºÅ",
                      "warning"
                    );
                    return false;
                  }
                },
              },
            ],
            consequence: () => {
              this.gameState.hiveHealth -= 20;
              if (this.gameState.bees > 0) this.gameState.bees--;
              this.showNotification("ËúÇËû®Âç±Ê©üÊÉ°ÂåñÔºÅËúúËúÇÊï∏ÈáèÊ∏õÂ∞ë", "danger");
              this.renderBees();
            },
          },
          {
            id: "hornet",
            title: "ËÉ°ËúÇÂÖ•‰æµ",
            description: "‰∏ÄÁæ§ËÉ°ËúÇÊ≠£Âú®ÊîªÊìä‰Ω†ÁöÑËúÇÂ∑¢ÔºåËúúËúÇÊÅêÊÖå‰∏≠ÔºÅ",
            severity: "medium",
            solutions: [
              {
                name: "Ë®≠ÁΩÆËÉ°ËúÇÈô∑Èò±",
                cost: 100,
                effectText: "Èô∑Èò±ÊàêÂäüÊçïÁç≤ËÉ°ËúÇÔºåÂ®ÅËÑÖËß£Èô§",
                effect: () => {
                  this.gameState.money -= 100;
                  return true;
                },
              },
              {
                name: "ÊâãÂãïÈ©ÖË∂ï (ÈúÄË¶ÅÂãûÂäõ)",
                cost: 0,
                effectText: "Á∂ìÈÅéËæõËã¶È©ÖË∂ïÔºåËÉ°ËúÇÊö´ÊôÇÈõ¢Èñã‰∫Ü",
                effect: () => {
                  // Ê®°Êì¨ÈúÄË¶ÅÈáçË§áÈªûÊìäÊâçËÉΩÊàêÂäüÁöÑÂãûÂãïÈÅéÁ®ã
                  this.startLaborTask("È©ÖË∂ïËÉ°ËúÇ", 10, () => {
                    return true;
                  });
                  return false; // ËøîÂõûfalseÂõ†ÁÇ∫‰ªªÂãôÂ∞öÊú™ÂÆåÊàê
                },
              },
            ],
            consequence: () => {
              let lossBees = Math.max(1, Math.floor(this.gameState.bees * 0.2));
              this.gameState.bees -= lossBees;
              this.showNotification(
                `ËÉ°ËúÇÊîªÊìäÈÄ†Êàê${lossBees}ÈöªËúúËúÇÊêçÂ§±ÔºÅ`,
                "danger"
              );
              this.renderBees();
            },
          },
        ];
      },

      // ÂàùÂßãÂåñÁí∞Â¢ÉÂΩ±ÈüøÈ°ûÂûã
      initEnvironmentalEvents() {
        this.environmentalEvents = [
          {
            id: "pesticide",
            title: "Âë®ÂúçËæ≤Áî∞Âô¥ÁÅëËæ≤Ëó•",
            description: "ÈÑ∞ËøëËæ≤Áî∞Ê≠£Â§ßÈáèÂô¥ÁÅëËæ≤Ëó•Ôºå‰Ω†ÁöÑËúúËúÇÂèØËÉΩÊúÉ‰∏≠ÊØíÔºÅ",
            severity: "high",
            solutions: [
              {
                name: "Á∑äÊÄ•ÈÅ∑ÁßªËúÇÁÆ±",
                cost: 200,
                effectText: "ËúÇÁÆ±Â∑≤ÈÅ∑ÁßªËá≥ÂÆâÂÖ®Âú∞ÈªûÔºå‰ΩÜÈÅéÁ®ã‰∏≠ÊêçÂ§±‰∫Ü‰∏Ä‰∫õËúÇËúú",
                effect: () => {
                  this.gameState.money -= 200;
                  this.gameState.honey *= 0.7; // ÊêçÂ§±30%ËúÇËúú
                  return true;
                },
              },
              {
                name: "ÈóúÈñâËúÇÁÆ±ÂÖ•Âè£ (Êö´ÂÅúÁîüÁî¢)",
                cost: 0,
                effectText: "ËúÇÁÆ±ÂÖ•Âè£Â∑≤ÈóúÈñâÔºåËúúËúÇÂÆâÂÖ®‰ΩÜÁÑ°Ê≥ïÁîüÁî¢",
                effect: () => {
                  // Êö´ÂÅúÁî¢Ëúú1ÂàÜÈêò
                  const originalMultiplier =
                    this.gameState.weather.effectMultiplier;
                  this.gameState.weather.effectMultiplier = 0;
                  this.showNotification("ËúÇÁÆ±ÂÖ•Âè£ÈóúÈñâÔºåÁî¢ËúúÊö´ÂÅú1ÂàÜÈêò", "info");
                  setTimeout(() => {
                    this.gameState.weather.effectMultiplier =
                      originalMultiplier;
                    this.showNotification("ËúÇÁÆ±ÂÖ•Âè£Â∑≤ÈáçÊñ∞ÈñãÂïü", "info");
                  }, 60000);
                  return true;
                },
              },
            ],
            consequence: () => {
              let lossBees = Math.max(2, Math.floor(this.gameState.bees * 0.3));
              this.gameState.bees -= lossBees;
              this.gameState.hiveHealth -= 15;
              this.showNotification(
                `${lossBees}ÈöªËúúËúÇÂõ†Ëæ≤Ëó•‰∏≠ÊØíÊ≠ª‰∫°ÔºÅËúÇÁæ§ÂÅ•Â∫∑ÂèóÊêç`,
                "danger"
              );
              this.renderBees();
            },
          },
        ];
      },
    },
  };
</script>

<style>
  /* ÂåØÂÖ•ÂéüÂßãCSSÊ®£Âºè */
  * {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: "ÂæÆËªüÊ≠£ÈªëÈ´î", Arial, sans-serif;
  }

  body {
    background-color: #f5f0e5;
    color: #453018;
    line-height: 1.6;
  }

  .game-container {
    max-width: 900px;
    margin: 20px auto;
    padding: 20px;
    background-color: rgba(255, 255, 255, 0.9);
    border-radius: 15px;
    box-shadow: 0 0 15px rgba(0, 0, 0, 0.2);
  }

  header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;
    padding-bottom: 10px;
    border-bottom: 2px solid #ffd24d;
  }

  h1 {
    color: #d4a017;
    font-size: 2.2rem;
    text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.2);
  }

  h2 {
    color: #b38728;
    font-size: 1.5rem;
    margin-bottom: 10px;
    border-bottom: 1px solid #ffd24d;
    padding-bottom: 5px;
  }

  /* Â§©Ê∞£È°ØÁ§∫ */
  .weather-display {
    display: flex;
    align-items: center;
    gap: 10px;
    font-size: 1.2rem;
    padding: 5px 15px;
    background-color: #e9f5ff;
    border-radius: 20px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  }

