<template>
    <div class="labor-overlay" v-if="task.isActive">
      <div class="labor-task">
        <h3>{{ task.name }}</h3>
        <div class="progress-container">
          <div class="progress-bar" :style="{ width: progress + '%' }"></div>
        </div>
        <button 
          @click="performWork" 
          class="game-button"
          :disabled="isCompleted">
          {{ buttonText }}
        </button>
      </div>
    </div>
  </template>
  
  <script>
  export default {
    name: 'LaborTaskModal',
    props: {
      task: {
        type: Object,
        required: true
      }
    },
    data() {
      return {
        isCompleted: false,
        localClicksDone: 0 // Âú®Êú¨Âú∞ËøΩËπ§ÈªûÊìäÊ¨°Êï∏ÔºåËÄå‰∏çÊòØ‰øÆÊîπ prop
      };
    },
    computed: {
      progress() {
        // ‰ΩøÁî®Êú¨Âú∞Ë®àÊï∏Âô®‰æÜË®àÁÆóÈÄ≤Â∫¶
        return ((this.localClicksDone + this.task.clicksDone) / this.task.clicksNeeded) * 100;
      },
      buttonText() {
        if (this.isCompleted) {
          return 'ÂÆåÊàêÔºÅ';
        } else {
          // ‰ΩøÁî®Êú¨Âú∞Ë®àÊï∏Âô®Âíå prop Á∏ΩÂíå‰æÜÈ°ØÁ§∫Ââ©È§òÊ¨°Êï∏
          return `Âä™ÂäõÂ∑•‰Ωú (ÈÇÑÈúÄ ${this.task.clicksNeeded - this.task.clicksDone - this.localClicksDone} Ê¨°)`;
        }
      }
    },
    methods: {
      performWork() {
        if (this.isCompleted) return;
        
        // Êõ¥Êñ∞Êú¨Âú∞ÈªûÊìäÊ¨°Êï∏
        this.localClicksDone++;
        
        // Ê™¢Êü•ÊòØÂê¶ÂÆåÊàê‰ªªÂãô
        if ((this.localClicksDone + this.task.clicksDone) >= this.task.clicksNeeded) {
          this.isCompleted = true;
          
          // Âª∂ÈÅ≤ÈóúÈñâÊ®°ÊÖãÊ°ÜÔºåÁµ¶Áî®Êà∂‰∏ÄÂÄãË¶ñË¶∫ÂèçÈ•ã
          setTimeout(() => {
            // ÈÄöÁü•Áà∂ÁµÑ‰ª∂‰ªªÂãôÂÆåÊàêÔºå‰∏¶ÂÇ≥ÈÅûÊú¨Âú∞Á¥ØÁ©çÁöÑÈªûÊìäÊ¨°Êï∏
            this.$emit('complete', this.localClicksDone);
          }, 500);
        } else {
          // Âç≥‰ΩøÊú™ÂÆåÊàêÔºå‰πüÂèØ‰ª•ÂÆöÊúüÈÄöÁü•Áà∂ÁµÑ‰ª∂ÈÄ≤Â∫¶Êõ¥Êñ∞
          this.$emit('update-progress', this.localClicksDone);
        }
      }
    },
    watch: {
      // Áï∂ task ÊîπËÆäÊôÇÈáçÁΩÆÊú¨Âú∞ÁãÄÊÖã
      'task': {
        handler() {
          this.localClicksDone = 0;
          this.isCompleted = false;
        },
        deep: true
      }
    }
  }
  </script>
  
  <style scoped>
  .labor-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.7);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
  }
  
  .labor-task {
    background-color: white;
    padding: 20px;
    border-radius: 10px;
    text-align: center;
    max-width: 400px;
    box-shadow: 0 5px 20px rgba(0, 0, 0, 0.3);
  }
  
  h3 {
    margin-bottom: 15px;
    color: #5d4215;
  }
  
  .progress-container {
    width: 100%;
    height: 20px;
    background-color: #e0e0e0;
    border-radius: 10px;
    overflow: hidden;
    margin-bottom: 15px;
  }
  
  .progress-bar {
    height: 100%;
    background-color: #4caf50;
    transition: width 0.3s;
    width: 0%;
  }
  
  .game-button {
    margin: 0 auto;
    display: block;
  }
  
  .game-button:disabled {
    background-color: #4caf50;
    cursor: default;
  }
  </style>